import { ProductsTable } from './OrderItem';
import { Elements } from 'katejs/lib/client';
import moment from 'moment';

export default Form => class InvoiceItem extends Form {
  static doc = true;
  constructor(args) {
    super(args);
    this.productTable = new ProductsTable(this);
    this.actions.push({
      type: Elements.BUTTON,
      title: 'Print',
      onClick: () => this.print(),
    });
    if (this.app.allow('Order', 'put')) {
      this.actions.push({
        type: Elements.BUTTON,
        title: 'Order',
        onClick: () => this.order(),
      });
    }
  }
  afterInit() {
    if (super.afterInit) {
      super.afterInit();
    }
    if (!this.uuid) {
      this.content.client.value = this.app.vars.currentClient;
      this.app.vars.currentClient = undefined;
    }
  }
  async print() {
    await this.save();
    const { response: doc } = await this.app.Invoice.get({ uuid: this.uuid });
    const data = {
      ...doc,
      date: moment(doc.date).format('DD.MM.YYYY'),
      ...this.app.settings,
      products: doc.products.map(item => ({
        ...item,
        price: item.price.toFixed(2),
        sum: item.sum.toFixed(2),
      })),
      total: doc.total.toFixed(2),
    };
    this.app.print({ template: 'Invoice', data });
  }
  close() {
    window.history.back();
  }
  async order() {
    await this.save();
    this.app.vars.currentInvoiceData = this.getValues();
    this.app.open('OrderItem', {});
  }
}

